.signup-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #f0f0f0; /* Light gray background */
}

.signup-box {
  background-color: white;
  padding: 2rem;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
  width: 300px;
}

h2 {
  margin-bottom: 1rem;
  font-size: 1.5rem;
}

.note {
  margin-bottom: 1.5rem;
  font-size: 0.9rem;
  color: #555;
}

.input-group {
  margin-bottom: 1rem;
  text-align: left;
}

label {
  margin-left: 0.5rem;
  font-size: 1rem;
}

input[type="radio"] {
  margin-right: 0.5rem;
}

.signup-button {
  width: 100%;
  padding: 0.7rem;
  background-color: #007bff; /* Bootstrap blue */
  color: white;
  border: none;
  border-radius: 4px;
  font-size: 1rem;
  cursor: pointer;
}

.signup-button:hover {
  background-color: #0056b3; /* Darker blue on hover */
}
